import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		user :{
					phno: '',
					uid : ''
		}

	},
	mutations: {
		chumma(state , payload){
			state.user.phno = payload.phoneNumber
			state.user.uid = payload.uId
			}
	},
	// getters : {
	// 	getPhno : (state) => state.user.phno,
	// 	getUid : (state) => state.user.uid
	// }
})